/* Google Fonts 추가 */
@import url('https://fonts.googleapis.com/css2?family=Cinzel:wght@400;700&family=Playfair+Display:wght@400;700&display=swap');

/* 다크모드 배경 설정 */
body {
  background-color: #2c2c2c;
  color: #f0f0f0;
  font-family: 'Cinzel', serif;  /* 세련되고 볼릭한 폰트 */
}

.App {
  text-align: center;
  padding: 20px;
}

h1 {
  font-size: 3rem;
  font-family: 'Playfair Display', serif; /* 대제목에 볼릭한 폰트 적용 */
  font-weight: 700;
}

h2 {
  font-size: 2rem;
  font-weight: 700;
  margin-bottom: 20px;
}

/* 작은 글씨 스타일 */
.small {
  font-size: 0.8rem;
  color: #999;
  font-weight: 400;
}

/* 영화 리스트 스타일 */
.movie-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
  padding: 20px;
}

.movie-card {
  background-color: #3e3e3e;
  padding: 15px;
  border-radius: 15px;
  width: 200px;
  text-align: center;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  color: #fff;
  transition: transform 0.3s ease;
}

.movie-card:hover {
  transform: scale(1.05);  /* 영화 패널을 살짝 확대 */
}

.movie-poster {
  width: 100%;
  border-radius: 10px;
}

.movie-details h3 {
  margin: 10px 0;
  font-weight: 700;
  font-family: 'Cinzel', serif;
}

button {
  padding: 20px 40px;  /* 버튼 크기 확대 */
  margin: 10px;
  background: linear-gradient(135deg, #7b42f6, #42c3f6);
  color: white;
  border: none;
  border-radius: 12px;
  cursor: pointer;
  transition: all 0.3s ease;
}

button:hover {
  transform: scale(1.1);  /* 버튼 호버 시 살짝 확대 */
  background: linear-gradient(135deg, #42c3f6, #7b42f6);
}

.categories {
  display: flex;
  justify-content: center;
  overflow-x: auto;
  gap: 15px;
  padding: 10px;
}

/* 모바일 화면을 위한 반응형 CSS */
@media (max-width: 768px) {
  .movie-list {
    flex-direction: column;
    align-items: center;
  }

  .movie-card {
    width: 100%;
    margin-bottom: 20px;
  }

  button {
    width: 100%;
    padding: 15px;
  }
}

@media (max-width: 480px) {
  h1, h2 {
    font-size: 1.5rem;
  }

  .movie-card {
    padding: 10px;
    width: 90%;
  }
}
